import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { CollapseModule } from 'ngx-bootstrap/collapse';




import { AppComponent } from './app.component';
import { TopnavComponent } from './topnav/topnav.component';
import { NanniesComponent } from './pages/nannies/nannies.component';
import { AppRoutingModule } from './app-routing/app-routing.module';
import { LandingComponent } from './pages/landing/landing.component';
import { NannyListItemComponent } from './pages/nannies/nanny-list-item/nanny-list-item.component';
import { KeyStatsCardRowComponent } from './pages/nannies/key-stats-card-row/key-stats-card-row.component';

@NgModule({
  declarations: [
    AppComponent,
    TopnavComponent,
    NanniesComponent,
    LandingComponent,
    NannyListItemComponent,
    KeyStatsCardRowComponent,
  ],
  imports: [
    BrowserModule,
    CollapseModule.forRoot(),
    AppRoutingModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



